<template>
  <nav-bar>
    <span slot="title" class="title">订单详情</span>
  </nav-bar>
  <table class="table">
    <tbody>
      <tr>
        <td>
          订单号：{{order.sn}}
        </td>
        <td>总额：￥{{order.total_price}}</td>
        <td>实收：¥{{order.collect}}</td>
        <td>
          状态：{{order.status_text}}
        </td>
      </tr>
      <tr>
        <td colspan="4">
          <table class="table">
            <thead>
              <th>#</th>
              <th>图片</th>
              <th>名称</th>
              <th>单价</th>
              <th>数量</th>
              <th>状态</th>
            </thead>
            <tbody>
              <tr v-for="order_product in order.order_products">
                <td>{{order_product.id}}</td>
                <td>
                  <img alt="店铺头像" class="img-circle" width="40" height="40" v-bind:src="order_product.product.avatar" />
                </td>
                <td>{{order_product.product.name}}</td>
                <td>¥ {{order_product.product.price}}</td>
                <td>{{order_product.quantity}}</td>
                <td>{{order_product.status_text}}</td>
              </tr>
            </tbody>
          </table>
        </td>
      </tr>
    </tbody>
  </table>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        order: {}
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/orders/one.json', { access_token: this.access_token, order_id: params.order_id }).then(function (response) {
          transition.next({
            order: response.data
          })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



